@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap');

@font-face {
  font-family: KTFJermilov;
  src: url(./assets/fonts/KTFJermilov-Solid.ttf);
}

html {
  scroll-behavior: smooth;
}

.App {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
}

.title {
  font-family: KTFJermilov;
}

.parallax__title {
  color: #fff;
}

.divider {
  width: 100%;
  height: 100vh;
}

.product__btn {
  background: rgba(0, 0, 0, 0.5);
  cursor: pointer;
  transition: all ease 0.6s;
}

.product__btn:hover {
  background: rgb(0, 0, 0);
  cursor: pointer;
  transition: all ease 0.6s;
}

.product__btn_orange {
  background: rgba(248, 134, 84, 0.5);
  cursor: pointer;
  transition: all ease 0.6s;
}

.product__btn_orange:hover {
  background: rgb(248, 134, 84);
  cursor: pointer;
  transition: all ease 0.6s;
}

.filter_item {
  fill: #fff;
}

.filter_box:hover .filter_circle {
  fill: rgb(248, 134, 84);
  transition: 'all ease 0.4s';
}

.filter_box:active .filter_circle {
  fill: rgb(223, 38, 10);
  transition: 'all ease 0.4s';
}

@media (max-width: 5000px) {
  html {
    font-size: 40px;
  }
}

@media (max-width: 1450px) {
  html {
    font-size: 12px;
  }
}

@media (max-width: 1150px) {
  html {
    font-size: 10px;
  }
}

@media (max-width: 950px) {
  html {
    font-size: 9px;
  }
}

@media (max-width: 800px) {
  .divider {
    background-image: src('/images/divider02(tablet).jpg');
  }
}

@media (max-width: 800px) {
  html {
    font-size: 8px;
  }
}

@media (max-width: 700px) {
  html {
    font-size: 7px;
  }
}

@media (max-width: 600px) {
  html {
    font-size: 6px;
  }
}

@media (max-width: 500px) {
  html {
    font-size: 5px;
  }
}

@media (max-width: 400px) {
  html {
    font-size: 4px; /* 12px for mobile later */
  }
}

@media (max-width: 300px) {
  html {
    font-size: 3px;
  }
}

@media (max-height: 657px) {
  .divider {
    height: 110vh;
  }
}

@media (max-height: 600px) {
  .divider {
    height: 150vh;
  }
}

@media (max-height: 490px) {
  .parallax__title {
    color: #000;
  }
}

@media (max-height: 440px) {
  .divider {
    height: 170vh;
  }
}

@media (max-height: 400px) {
  .divider {
    height: 190vh;
  }
}
